<template>
  <!--  -->
  <div>
    <el-row :gutter="20">
      <el-col :span="12">
        <div class="grid-content">
          <div class="grid_title"></div>
          <div id="Piechart"></div>
        </div>
      </el-col>
      <el-col :span="12">
        <div class="grid-content">
          <div class="grid_title"></div>
          <div id="Columnar"></div>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { onMounted, watch } from "vue";
import { Pie, Column } from "@antv/g2plot";
export default {
  props: {
    pieChert: Array,
    hisTogram: Array,
  },
  setup(props) {
    // 
    function pieChart(data) {
      const piePlot = new Pie("Piechart", {
        appendPadding: 10,
        data,
        angleField: "value",
        colorField: "name",
        radius: 0.8,
        label: {
          type: "outer",
        },
        interactions: [{ type: "element-active" }],
      });

      piePlot.render();
    }
    // 
    function coLumnar(data) {
      const columnPlot = new Column("Columnar", {
        data,
        xField: "age",
        yField: "sales-volume",
        label: {
          //  label 
          position: "middle", // 'top', 'bottom', 'middle',
          // 
          style: {
            fill: "#eee",
            opacity: 0.9,
          },
        },
        meta: {
          age: {
            alias: "",
          },
          "sales-volume": {
            alias: "",
          },
        },
      });
      columnPlot.render();
    }
    onMounted(() => {});

    // 
    watch(props, (newVal, oldVal) => {
      pieChart(newVal.pieChert);
      coLumnar(newVal.hisTogram);
    });

    return {
      pieChart,
      coLumnar,
    };
  },
};
</script>
<style   scoped>
.el-row {
  margin-bottom: 20px;
}
.grid-content {
  background-color: #ffffff;
}
.grid_title {
  font-weight: bold;
  padding: 10px;
  border-bottom: 1px solid #eee;
  margin-top: 20px;
}
</style>